import React, { Component } from 'react'
import './app.css'
export default class App extends Component {

    constructor(props) {
        super(props)
        this.handleClickMais = this.handleClickMais.bind(this)
        this.state = {
            name: 'Matheus',
            counter: 0
        }
    }
    
    handleClickMais= () => {
        const { counter } = this.state
        this.setState({ counter: counter + 1})
    }

    handleClickMenos= () => {
        const { counter } = this.state
        this.setState({ counter: counter - 1})
    }

    

    render(){

        const { name, counter} = this.state

        return(
            <>
            <div className="App">
            <h1>Contador Estudos React.JS</h1>
            <button className="btn" onClick={this.handleClickMais}>+</button>
            <h1>{counter}</h1>
            <button className="btn" onClick={this.handleClickMenos}>-</button>
            </div>
            </>


        )
    }

}